.header-search-input {
    padding: 0 0 0 24px;
    overflow: hidden;
}

[contenteditable='true']#header-search-inputbox {
    white-space: nowrap;
    overflow: hidden;
}
[contenteditable='true']#header-search-inputbox br {
    display: none;
}
[contenteditable='true']#header-search-inputbox * {
    display: inline;
    white-space: nowrap;
}

#header-search-inputbox {
    box-shadow: 0 0 0 0;
    padding: 22px 0 22px 0;
    white-space: pre-wrap;
}

#header-search-inputbox::before {
    /*content: attr(placeholder);*/
    color: #8e9396;
    pointer-events: none;
    -webkit-transform: translateX(20px);
            transform: translateX(20px);
    will-change: transform;
    opacity: 0;
    /*position: absolute;*/
    transition: opacity 0.15s ease-out, -webkit-transform 0.15s ease-out;
    transition: opacity 0.15s ease-out, transform 0.15s ease-out;
    transition: opacity 0.15s ease-out, transform 0.15s ease-out, -webkit-transform 0.15s ease-out;
}

#header-search-inputbox:empty::before {
    content: attr(placeholder);
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    opacity: 0.75;
}

#header-search-inputbox:empty:active::before,
#header-search-inputbox:empty:focus::before {
    opacity: 0.75;
}

#header-search-inputbox:focus {
    border: 0;
    outline: none;
    box-shadow: 0 0 0 0;
}

